import React from "react";
import "../Styles/ImagenDegradada.css";
// Asegrate de tener un archivo CSS donde agregars estilos personalizados

function ImagenDegradada() {
  return (
    <div className="container">
      <div className="image-container">
        <img src="/img_perfil.webp" alt="Es una imagen degradadax" />
        <div className="overlay"></div>
        <div className="overlay1"></div>
        <div className="overlay2"></div>
        <div className="overlay3"></div>
      </div>
    </div>
  );
}

export default ImagenDegradada;
