import React from "react";
import "./styles/BtnPerfil.css";

function BtnPerfil({ onClick, txt}) {
  return (
    <button class="btn" type="button" onClick={onClick}>
      <strong>{txt}</strong>
      <div id="container-stars">
        <div id="space"></div>
      </div>

      <div id="glow">
        <div class="circle"></div>
        <div class="circle"></div>
      </div>
    </button>
  );
}

export default BtnPerfil;
