import React from 'react';
import Lottie from 'react-lottie';
import animationData from './Animation - 1708207276422'; // Ruta al archivo de animacin JSON

function LottieDevelopment() {

    const getSize = () => {
        if (window.innerWidth <= 700) { // Si el ancho de la ventana es menor o igual a 768px (tamao de tablet)
          return { width: 400, height: 400 };
        } else if(window.innerWidth <= 1020 && window.innerWidth > 700){ // Si es mayor a 768px (tamao de celular)
          return { width: 550, height: 550 };
        }else{
            return { width: 700, height: 700 };
        }
      };

  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice'
    }
  };

  const { width, height } = getSize();

  return (
    <div>
      <Lottie options={defaultOptions} height={height} width={width}/>
    </div>
  );
}

export default LottieDevelopment;