import React, { Fragment } from "react";
import "./styles/ButtonStyle1.css"
import { useNavigate } from 'react-router-dom';


function ButtonDrawel(props) {

  const navigate = useNavigate();

  const handleClick = () => {
    // Navega a la pgina 2
    navigate(props.link);

  };

  return (

    <button class="button" data-text="Awesome" onClick={handleClick}>
      <span class="actual-text">&nbsp;{props.text}&nbsp;</span>
      <span aria-hidden="true" class="hover-text">
        &nbsp;{props.text}&nbsp;
      </span>
    </button>
  );
}

export default ButtonDrawel;
