import React from "react";
import { motion } from "framer-motion";
import styles from "../../Styles/Home.module.css"
import OptionsDrawell from "../options";

const OptionsDrawellWithAnimation = () => {
    return (
      <motion.div
        className={styles.optionsDrawellContainer}
        initial={{ y: -1000 }} // Posicin inicial, fuera de la vista arriba
        animate={{ y: 0 }} // Posicin final, en su ubicacin original
        transition={{ duration: 1.7, ease: "easeInOut" }} // Duracin y tipo de transicin
      >
        <OptionsDrawell />
      </motion.div>
    );
  };

  export default OptionsDrawellWithAnimation;