import Hamburguesa from "./btnHamburguesa";
import Tarjeta from "./tarjetaDrawable";
import { Fragment, useEffect, useState } from "react";
import styles from "../Styles/Home.module.css"; // Importa los estilos CSS

function OptionsDrawell() {
  const [showTarjeta, setShowTarjeta] = useState(false);
  let a = 0;
  const toggleTarjeta = () => {
    a = a +1;
    if(a == 2){
      console.log("Entro");
      setShowTarjeta(!showTarjeta);
      a = 0;
    }
  };

  return (
    <div>
      {showTarjeta && (
        <div className={styles.tarjetaContainer}>
          <Tarjeta />
        </div>    
      )}
      <div
        className={styles.hamburguesaContainer}
        onClick={toggleTarjeta}
      >
        <Hamburguesa />
      </div>
    </div>
  );
}

export default OptionsDrawell;
