import React from 'react';
import { motion } from 'framer-motion';

const SlidingDoorsAnimation = () => {
  return (
    <div style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', zIndex: 4 }}>
          <motion.img
            src="/izq.jpg" // Ruta de la primera imagen
            style={{ width: "50vw", height: "100vh" }}
            animate={{ x: [-2000, 0, -2000] }}
            transition={{ duration: 2, ease: "easeInOut" }}
          />
          <motion.img
            src="/der.jpg" // Ruta de la segunda imagen
            style={{  width: "50vw", height: "100vh" }}
            animate={{ x: [2000, 0, 2000] }}
            transition={{ duration: 2, ease: "easeInOut" }}
          />
        </div>
  );
};

export default SlidingDoorsAnimation;
