import React, { Fragment, useState, useEffect } from "react";
import { Routes, Route, useLocation, Navigate } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import Home from "./pages/Home";
import Perfil from "./pages/Perfil";
import Experiencia from "./pages/Experiencia";
import Proyectos from "./pages/Proyectos";
import Mas from "./pages/Mas";
import SlidingDoorsAnimation from "./components/animation/SlidingDoorsAnimation";

function App() {
  const [isLoading, setIsLoading] = useState(false);
  const [targetPage, setTargetPage] = useState(null);
  const location = useLocation();
  const [isAnimationVisible, setIsAnimationVisible] = useState(true);
 

  useEffect(() => {
    setIsLoading(true); // Empieza la animacin de las imgenes al mismo tiempo que la animacin del router
    setIsAnimationVisible(true);
    if (targetPage) {
      setTimeout(() => {
        setIsLoading(false);
        setTargetPage(null);
        console.log("funciona cambio");
      }, 1000); // Espera un segundo antes de cambiar de pgina
    }
   
      setTimeout(() => {
        setIsAnimationVisible(false);
      }, 3000);
      console.log(isAnimationVisible);
    
  }, [location.pathname]); // Ejecuta el efecto cuando cambia la ubicacin

  

  const handleNavigation = (target) => {
    setTargetPage(target);
  };

  return (
    <Fragment>
      <AnimatePresence
        exitBeforeEnter={false}
        mode="wait"
        style={{ backgroundColor: "black" }}
      >
        <Routes
          location={location}
          key={location.pathname}
          style={{ backgroundColor: "black" }}
        >
          <Route
            path=""
            style={{ backgroundColor: "black" }}
            element={
              <motion.div
                style={{ backgroundColor: "black" }}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1, opacity: { duration: 1 } }}
              >
                <SlidingDoorsAnimation />
                <Home />
              </motion.div>
            }
          />
          <Route
            path="/Perfil"
            element={
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1, opacity: { duration: 1 } }}
              >
                <SlidingDoorsAnimation />
                <Perfil />
              </motion.div>
            }
          />
          <Route
            path="/Experiencia"
            element={
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1, opacity: { duration: 1 } }}
              >
                {isAnimationVisible && <SlidingDoorsAnimation />}
                <Experiencia />
              </motion.div>
              
            }
          />
          <Route
            path="/Proyectos"
            element={
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1, opacity: { duration: 1 } }}
              >
                {isAnimationVisible && <SlidingDoorsAnimation />}
                <Proyectos />
              </motion.div>
              
            }
          />
          <Route
            path="/Mas"
            element={
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1, opacity: { duration: 1 } }}
              >
                {isAnimationVisible && <SlidingDoorsAnimation />}
                <Mas />
              </motion.div>
              
            }
          />
        </Routes>
      </AnimatePresence>

      

      {/*isLoading && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            zIndex: 4,
          }}
        >
          <motion.img
            src="/izq.jpg" // Ruta de la primera imagen
            style={{ width: "50vw", height: "100vh" }}
            animate={{ x: [-2000, 0, -2000] }}
            transition={{ duration: 2, ease: "easeInOut" }}
          />
          <motion.img
            src="/der.jpg" // Ruta de la segunda imagen
            style={{ width: "50vw", height: "100vh" }}
            animate={{ x: [2000, 0, 2000] }}
            transition={{ duration: 2, ease: "easeInOut" }}
          />
        </div>
      )*/}

      {targetPage && <Navigate to={targetPage} />}
    </Fragment>
  );
}

export default App;
